import React from 'react';
import MergedPR from './MergedPR'

const styles = {
    contentHeaderMenuLink: {
        textDecoration: 'none',
        color: 'white',
        padding: 8,
    },
    content: {
        padding: '16px',
    },
    logo: {
        width: '80px',
        paddingRight: '50px',
        paddingLeft: '30px',
        verticalAlign: 'bottom',
    },
    header: {
        backgroundColor: 'black',
        color: 'white',
        padding: '16px',
        fontSize: '1.5em',
        borderBottom: 'solid',
        borderBottomColor: '#ff820e',
        width: '100%',
        marginLeft:'auto',
        marginRight:'auto',
    },
    MergedPR: {
        width: '90%',
        marginLeft:'auto',
        marginRight:'auto',
    }
};



class App extends React.Component {
    // constructor(props) {
    //     super(props);
    // }

    render() {
        const contentHeader = (
            <div style={styles.header}>
                <span>
                    <span>
                        <img style={styles.logo}
                             src={process.env.PUBLIC_URL + '/assets/images/wso2-logo.svg'}
                             alt={'WSO2'}/>
                    </span>
                    <span>
                        <b>GitHub Dashboard - Merged PRs</b>
                    </span>
                </span>
            </div>
        );


        return (
            <div>
                {contentHeader}
                <br/>
                <br/>
                <div style={styles.MergedPR}>
                    <MergedPR/>
                </div>
            </div>
        );
    }
}

export default App;


